<template>
  <svg  :class="className" aria-hidden="true"  >
    <use :xlink:href="iconName" />
  </svg>
</template>

<script>
export default {
  name: 'SvgIcon',
  props: {
    iconClass: {
      type: String,
      required: true,
    },
    className: {
      type: String,
      default: '',
    },
  },
  computed: {
    iconName() {
      return `#icon-${this.iconClass}`;
    },
    svgClass() {
        return this.className ? `svg-icon ${this.className}` : 'svg-icon';
      }
  },
};
</script>

<style scoped>
  .svg-icon {
  width: 10em;
  height: 10em;
  fill: #ca0c0c; /* 设置为黑色 */
  vertical-align: -0.15em;
  overflow: hidden;
}
  </style>