import axios from 'axios';

// 创建 Axios 实例
const service = axios.create({
  baseURL: process.env.VUE_APP_API_BASE_URL, // 根据环境变量动态设置 baseURL
  timeout: 600000, // 请求超时时间
  headers: { "Content-Type": "application/json" },
  //  withCredentials: true,
});

// 请求拦截器
service.interceptors.request.use(
  (config) => {
    // 在发送请求前可以添加一些逻辑
    return config;
  },
  (error) => {
    // 请求错误处理
    return Promise.reject(error);
  }
);

// 响应拦截器
service.interceptors.response.use(
  (response) => {
    // 如果是下载接口，返回完整的 response 对象
    if (response.config.url.includes('/api/download.php')||
    response.config.url.includes('/api/download-reinforced.php')) {
      return response;
    }
    // 其他接口返回 response.data
    return response.data;
  },
  (error) => {
    // 响应错误处理
    return Promise.reject(error);
  }
);

export default service;
