import { createRouter, createWebHistory } from 'vue-router';

import apiRequest from '@/utils/request';

const routes = [
  {
    path: '/login',
    component: () => import('@/views/LoginPage'),
    hidden: true,
  },
  {
    path: '/',
    component: () => import('@/layout/AppLayout.vue'),
    redirect: '/home',
    children: [
      {
        path: 'home',
        component: () => import('@/views/HomePage.vue'),
        name: 'HomePage',
        meta: { title: '首页', icon: 'el-icon-house', requiresAuth: true },
      },
      {
        path: 'security',
        component: () => import('@/views/SecurityPage.vue'),
        name: 'SecurityPage',
        meta: { title: '代码加固', icon: 'el-icon-lock', requiresAuth: true },
      },
      {
        path: 'chat',
        component: () => import('@/views/ChatPage.vue'),
        name: 'ChatPage',
        meta: { title: '实时聊天', icon: 'el-icon-chat-dot-square', requiresAuth: true },
      },
      {
        path: 'data-management',
        component: () => import('@/views/DataManagement.vue'),
        name: 'DataManagement',
        meta: { title: '数据管理', icon: 'el-icon-data', requiresAuth: true },

        
      },
    ],
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

// 导航守卫：检查用户是否已登录
router.beforeEach(async (to, from, next) => {
  // const isAuthenticated = localStorage.getItem("isAuthenticated");
  const requiresAuth = to.meta.requiresAuth;
  const token = localStorage.getItem('auth_token');
  const userid = localStorage.getItem('Userid');

  console.log('Authorization:', token);
  console.log('user_id:', userid);

  if (requiresAuth) {
    try {
      const response = await apiRequest.get('api/check-login.php',{
        withCredentials: true, // 携带 Cookie
        headers: {
          // 'Authorization': `Bearer ${isAuthenticated}`  
          'Authorization': token,  // 带上Token 
          'Userid':userid,
          'Content-Type': 'application/json'}// 在Authorization头中传递认证信息
          
        }
        
      );
      const data = response;
      console.log(response);

      if (data.status === 'success') {
        // 用户已登录，继续访问
        next();
      } else {
        // 用户未登录，重定向到登录页面
        next({ path: '/login' });
      }
    } catch (error) {
      // 请求失败时，跳转到登录页面
      next({ path: '/login' });
    }
  } else {
    next(); // 如果该页面不需要认证，继续访问
  }
}

);

export default router;
